  "app.MoviesData.Interstellar_Title": "Интерстеллар",

  "app.MoviesData.Interstellar": "Действие переносит зрителя в недалекое будущее, когда космос уже почти освоен шагнувшим вперед человечеством, но ресурсы Земли истощены. Из-за растущей концентрации азота на нашей планете царит засуха и, как следствие, глобальный продовольственный кризис. Сотрудники секретной лаборатории НАСА обнаруживают в районе орбиты Сатурна червоточину, позволяющую преодолеть пространственно-временной барьер и в кратчайшие сроки переместиться в другую галактику. Возможно, это шанс на спасение для всего человечества. В рамках программы «Лазарь» совершается первая экспедиция, в результате которой по ту сторону так называемой кротовой норы обнаруживаются три наиболее подходящих для жизни планеты. Теперь настало время для второй экспедиции: профессору Бренду и его коллегам предстоит, рискуя собственными жизнями, провести исследования в условиях, когда один час равен нескольким годам на Земле, и найти способ переселения землян на другую планету. Смогут ли ученые спасти человечество...",
  
  "app.MoviesData.Passengers_Title": "Пассажиры (2016)",

  "app.MoviesData.Passengers": "Космический корабль с 5 000 пассажирами на борту, погруженными на время полета в искусственный сон, держит курс на планету, которая станет для них новым домом. Всё идет по плану, но из-за сбоя системы двое пассажиров неожиданно просыпаются и понимают, что до конечного пункта путешествия 90 лет пути, и им суждено остаток жизни провести вдвоем в роскошных интерьерах корабля, оснащенного по последнему слову техники. Постепенно Джима и Аврору начинает непреодолимо тянуть друг к другу, но корабль попадает в беду, и теперь в их руках – жизнь и судьба всех обитателей корабля.",
  
  "app.MoviesData.Martian_Title": "Марсианин (2015)",

  "app.MoviesData.Martian": "После сильнейшей бури на Марсе коллеги астронавта Марка Уотни посчитали его погибшим и покинули красную планету. Но Уотни выжил и оказался совсем один. Марк не пал духом и проявляет всю свою изобретательность, чтобы выдумать способ сообщить на Землю о своем положении. Теперь за миллионы километров от Марса  сотрудники НАСА и лучшие ученые из разных стран прилагают все усилия, чтобы вернуть «марсианина» домой, а члены его экипажа организуют практически невозможную спасательную миссию. Весь мир, затаив дыхание, следит за возвращением Уотни на родную планету.",
  
